<template>
  <div>
      <div slot="content">
        <div class="box">
          <div class="user-section">
            <img class="bg" src="@/assets/fourth/user-bg.png" />
            <div class="user-info-box">
              <div class="portrait-box" @click="goinfo">
                <img :src="imgbox[userInfo.headimg || 0].src" class="portrait" />
              </div>
              <div class="info-box">
                <p class="username">{{userInfo.name || name}}</p>
              </div>
            </div>
          </div>
          <div class="cover-container">
            <van-cell title="个人资料" icon="user-o" is-link to="userinfo" />
            <van-cell center title="好友上线通知" icon="bullhorn-o">
              <template #right-icon>
                <van-switch active-value="1" inactive-value="0" v-model="checked" size="24" @change='check' />
              </template>
            </van-cell>
            <van-cell title="密码设置" icon="setting-o" is-link @click="pwdPage" />
            <van-cell title="清空缓存" icon="warn-o" is-link @click="removeLast" />
            <van-cell title="关于我们" icon="info-o" is-link to="about" />
            <van-cell title="退出登录" icon="close" is-link @click="loginClick" />
          </div>
        </div>
      </div>
  </div>
</template>

<script>
import { Dialog } from 'vant';
import { Toast } from 'vant';
import {mapState, mapMutations} from 'vuex';
export default {
  name: "mine",
  data() {
    return {
      name: '游客',
      checked: 0,
      imgbox: [
        { id: 0, src: require("../../assets/fourth/manf.png") },
        { id: 1, src: require("../../assets/fourth/womanf.png") },
        { id: 2, src: require("../../assets/fourth/mans.png") },
        { id: 3, src: require("../../assets/fourth/womans.png") }
      ]
    }
  },
  computed: {
    ...mapState(['userInfo']),
  },
  mounted() {
    var value = localStorage.getItem('checked')
    this.check(value)
  },
  methods: {
    ...mapMutations(['logout']),
    check(value) {
      this.checked = value
      localStorage.setItem('checked', value)
    },
    loginClick() {
      this.$socket.emit('quit', this.userInfo.tel)
      this.logout()
      this.$router.push("/login");
    },
    goinfo() {
      this.$router.push("/userinfo");
    },
    pwdPage() {
      this.$router.push("/password");
    },
    removeLast() {
      Dialog.confirm({
        title: "清空缓存",
        message: "你确定要全部清空缓存吗?",
        confirmButtonText: "确定", //改变确认按钮上显示的文字
        cancelButtonText: "手滑了" //改变取消按钮上显示的文字
      })
        .then(() => {
          var arr = this.userInfo.friends
          for(var i = 0; i < arr.length; i++) {
            localStorage.removeItem(arr[i].tel)
          }
          Toast('完成')
        })
        .catch(() => {
          // on cancel
          Toast('取消操作')
        });
    }
  }
};
</script>
<style lang="scss" scoped>
.user-section {
  height: 100px;
  padding: 60px 0 50px 0;
  position: relative;
  display: flex;
  align-items: center;
  justify-content: center;
  .bg {
    position: absolute;
    left: 0;
    top: 0;
    width: 100%;
    height: 100%;
    filter: blur(1px);
  }
}

.cover-container {
  position: relative;
}
.user-info-box {
  height: 80px;
  display: flex;
  text-align: center;
  flex-direction: column;
  align-items: center;
  position: relative;
  z-index: 1;
  .info-box {
    text-align: center;
  }
  .portrait {
    width: 80px;
    height: 80px;
    border: 5px solid #fff;
    border-radius: 50%;
  }
  .username {
    font-size: 20px;
    color: #ffffff;
  }
}
</style>